@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto+Mono:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;1,100;1,200;1,300;1,400;1,500;1,600;1,700&family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


#heading_line{
    font-family: "Open Sans" !important;
    font-size: 45px;
    line-height: 150%;
    text-align: center;
    font-weight: 300;
}

.MuiOutlinedInput-input{
    border-radius: 0;
}

.MuiOutlinedInput-input:focus{
    border-radius: 0;
}
body{
    background-color: #FCFCFC;
    font-family: 'Roboto';
}

p {

    line-height: 150%;
    font-size: 18px;
    /*margin: 0;*/
    margin-bottom: 2.5rem;
}

h1 {
    font-family: "Roboto Mono";
    text-align: center;
    /*text-transform: uppercase;*/
    font-size: 55px;
    font-weight: normal;
}

h1 span {
    font-weight: normal!important;
}

a {
    color: #1648c6;
    text-decoration: none;
    /*background-image: linear-gradient(120deg, #1d85d2 0%, #dce1ff 100%);*/
    /*background-repeat: no-repeat;*/
    /*background-size: 100% 0.2em;*/
    /*background-position: 0 105%;*/
}

ol, ul {
    line-height: 150%;
    font-size: 18px;
    margin: 0;
    margin-bottom: 3rem;
}

.chapter_img {
    width: 100% !important;
}

li::marker {
    content: counter(list-item)  ". ";
    color: black;
    font-size: 1.2rem;
}

h2 {
    overflow: hidden;
    position: relative;
    margin-top: 5rem;
    font-weight: 600!important;
}
h2 span {
    font-weight: 600!important;
}

h2::after {
    content: '';
    width: 100%;
    height: 1px;
    background: #1d85d2;
    position: absolute;
    top: 50%;
    margin-left: 1rem;
}

.MuiButton-containedPrimary {
    background-color: #1d85d2 !important;
}

.MuiIconButton-root{
    color: black !important;
}

.navbar {
    padding: 1.5rem 2rem;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.avatar {
    background-color: #00A79D !important;
    /*border: black solid;*/
}

.navbar_controls{
    display: flex;
    justify-content: space-between;
    min-width: 20rem;
}
.responseQuestionBox {
    display: flex;
    flex-direction: row;
    align-items: center;
    /*border: black 0.5px solid;*/
    padding: 1.5rem;
    border-radius: 5px;
}

.responseQuestionBox span {
    line-height: 1.5;
}

.checkmark{
    margin: 0;
}
.p-no-margin {
    margin : 0;
}

.MuiDivider-root{
    margin-top: 1rem !important;
    margin-bottom: 1rem !important;
}

.responseQuestionBox{
    margin-top: 3rem;
    margin-bottom: 3rem;
}
.product {
    margin : 5rem 10rem
}

.bookCover{
    margin-right: 3rem;
    border: black 1px solid;
    height: 500px;
}

#bookTitle {
    font-size: 2.5rem;
}

#price {
    font-family: "Roboto Mono";
    font-size: 1rem;
}
